function PostDetail() {
  const { id } = useParams();
  const post = blogPosts.find(p => p.id === parseInt(id));

  if (!post) {
    return <p>포스트를 찾을 수 없습니다.</p>;
  }

  return (
    <main className="main-content">
      <article className="blog-post">
        <h2>{post.title}</h2>
        <p className="post-date">{post.date}</p>
        <p>{post.content}</p>
      </article>
    </main>
  );
}

export default PostDetail;
